import React, {useState} from 'react';
import { Button } from '@material-ui/core';

import { useQuery } from '@apollo/react-hooks';
import Link from 'next/link' ;

import {MAIN_QUERY, mainQueryCache} from './queries';
import client from './client'

import Welcome from "./Welcome"

import Myradio from './Myradio'
import Myresult from './Myresult.js'
import { UserContext } from './UserContext';

const App = () => {

  const [store, setStore] = useState('first');
  const { data } = useQuery(MAIN_QUERY,{
    variables: {
      artistId : 1
    }
  });

  console.log(data)
  return (
    <UserContext.Provider value={[store,setStore]}>
      <div className="App">
        <header className="App-header"> Select an artist</header>
        <Myradio />
        <Myresult data={data}/>
      </div>
    </UserContext.Provider>
  );
}

export default App;